<template>
    <div>
        <div>row:</div>
        <input type="number" v-model="m" @change="update">
        <div>col:</div>
        <input type="number" v-model="n" @change="update">
    </div>
</template>

<script setup>
import { useStore } from 'vuex';
import { ref } from 'vue';
const store = useStore();
const m = ref(store.state.row);
const n = ref(store.state.col);
const update = () => {
    store.commit("update", [m, n]);
}


</script>

